import compose from './../utils/compose';
import { style } from 'styled-system';

export const bgColor = style({
	prop: 'bg',
	cssProperty: 'backgroundColor',
	key: 'palette',
});

export const textColor = style({
	prop: 'color',
	cssProperty: 'color',
	key: 'palette',
});



